#landing {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../img/marble.jpg") no-repeat center center fixed;
    background-size: cover;
}    

.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: $base;
    border-radius: 5px;
    padding: 20px;
    border: 2px solid $dark-accent;
    box-shadow: 6px 6px $highlight;
    box-sizing: border-box;
    width: 80vw;
    p {
        color: $light-accent;
        font-size: 3em;
        font-weight: bold;
        margin: 0;
        text-align: center;
    }
    div {
        color: $dark-accent;
        font-size: 1.3em;
        font-weight: bold;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }
}

.fa-square-full {
    font-size: 0.28em;
}

.fa-angle-double-down {
    position: absolute;
    bottom: 20px;
    font-size: 4em;
    color: $light-accent;
}